<template>
  <div>
    <el-row style="margin: 5vh 0" >
      <el-col :span="6"
              class="aside-tree">
        <el-tree
          :props="props"
          accordion
          :load="loadNode"
          lazy
          @node-click="getDetail"
          highlight-current>
        </el-tree>
      </el-col>
      <el-col :span="16" :offset="1" class="aside-content">
          <div style="text-align: center;line-height: 100px;border-bottom: 1px #999999 solid;position: relative ">
            <i class="el-icon-question" @click="isInfo=true"></i>
            <el-button type="primary" @click="isFolder = true">增加模块</el-button>
            <el-button type="primary" @click="showModel()">增加模板</el-button>
          </div>
          <div>
            <el-row type="flex"  style="margin: 40px 20px 0" justify="space-between">
              <el-col class="mock-col" :span='8' v-for="(v,index) in this.modelList" :key="index" @click.native="getModelContent(v)">
                {{v.modelName}}
                <el-button type="primary" @click.stop="getHost(v.modelPath)" size="mini" style="float: right">获取地址</el-button>
              </el-col>
            </el-row>
          </div>
      </el-col>
    </el-row>
    <!--<el-dialog
      title="API数据模拟模板说明"
      :visible.sync="isInfo"
      width="500px"
      h
      center>
      <p style="line-height: 2"> 每行配置一个属性，格式为  propertyName[type|value|约束条件]，默认为string</p>
      <p style="line-height: 2">
        <p>property[int|value|min|max]</p>
        <p>property[string|value|length]</p>
        <p>property[decimal|value|length,precision]</p>
        <p>property[list|modelName|size]</p>
        <p>property[list|modelName|size]</p>
      </p>
      <p style="line-height: 2">
        示例1：简单的配置对象student
            <p>name</p>
            <p>age[int]</p>
            <p>class[string]</p>
      </p>
      <p style="line-height: 2">
        示例2：内嵌对象student
          <p>name</p>
          <p>age[int]</p>
          <p>clas[model|clas]</p>
          <p>@model_clas_start</p>
          <p>grade</p>
          <p>clas[int||1|10]</p>
          <p>@model_clas_end</p>
          <p> ls[list|clas|3]</p>
          <p>ext_std[model|student]  //外部model
       </p>
      </p>
      <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="isInfo=false" size="small">确定</el-button>
            </span>
    </el-dialog>-->
    <div class="mock_wrapper" v-show="isInfo" @click="isInfo=false">
      <iframe src="./static/info.html"
              class="mock_help"
              frameborder="0"
              ></iframe>
    </div>
    <el-dialog
      title="增加模块"
      :visible.sync="isFolder"
      width="500px"
      center>
      <el-form label-width="80px" >
        <el-form-item label="模块名称">
          <el-input v-model.trim="templateName" placeholder="模块名称" style="color: rgba(51,51,51,0.6);"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
                <el-button @click="isFolder = false" size="small">取消</el-button>
                <el-button type="primary" @click="addFolder" size="small">确定</el-button>
            </span>
    </el-dialog>

    <el-dialog
      :title="title"
      :visible.sync="isModel"
      custom-class="dc-dialog"
      width="500px"
      height="50vh"
      center>
      <el-form  label-width="80px" >
        <el-form-item label="模板名称" >
          <el-input v-model.trim="modelName" maxlength="20" placeholder="请输入模板名称" style="color: rgba(51,51,51,0.6);"></el-input>
        </el-form-item>
        <el-form-item label="模块内容" >
          <el-input v-model="templateContent"  placeholder="请输入模板内容"  type="textarea"
                    :autosize="{ minRows: 4, maxRows: 14}" style="color: rgba(51,51,51,0.6);"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
                <el-button @click="isModel = false" size="small">取消</el-button>
                <el-button type="primary" @click="addModel" size="small">确定</el-button>
            </span>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        isInfo: false,
        folderList: [],
        modelList: [],
        isModel: false,
        isFolder: false,
        modelName: '',
        templateContent: '',
        modelPath: '',
        title: '',
        folderPath: '',
        templateName: '',
        root: '',
        props: {
          label: 'folderName',
          id: 'folderPath',
          children: 'subNode',
          isLeaf: 'leaf'
        },
      }
    },
    created() {
      //this.getFoldList('aaa/');
    },
    methods: {
      getHost(v) {
        let req=window.location.host;
        let http=window.location.protocol
        let message=http+req+'/api/mockData?modelName='+v;
        this.$alert(message, '地址', {
          confirmButtonText: '确定',
          type: 'info '
        })
      },
      getFoldList(folderName) {
        this.$Ajax.post('/api/tmp/list', {
          folderName: folderName,
        }).then(res => {
          this.folderList=res.data

        })
      },
      getDetail(data) {
        this.folderPath=data.folderPath;
        if(data.modelList){
          this.modelList=data.modelList
        }

      },
      loadNode(node, resolve) {
        if (node.level === 0) {
          return resolve([{folderName: 'root',folderPath: ''}]);
        }
        this.$Ajax.post('/api/tmp/list', {
          folderName: node.data.folderPath
        }).then(res => {
          let folderList=[];
          let modelList=[];
          if(res.data.subNode&&res.data.subNode.length>0){
            res.data.subNode.forEach(u=>{
              if(u.folderName){
                folderList.push({
                  folderName: u.folderName,
                  folderPath: u.folderPath
                })
              }
              if(u.modelName){
                modelList.push({
                  modelName: u.modelName,
                  modelPath: u.modelPath
                })
              }
            })
          }
          this.modelList=modelList
          this.$set(node.data,'modelList',modelList)
          resolve(folderList)
        })
      },

      showModel(v){
        if(v){
          this.title='更新模块';
          this.templateContent=v.templateContent;
          this.modelName=v.modelName
        }else {
          this.title='增加模块';
          this.templateContent='';
          this.modelName=''
        }
        this.isModel=true
      },
      getModelContent(v){
        this.modelPath=v.modelPath;
        const loading = this.$loading({
          background: 'rgba(0, 0, 0, 0)'
        });
        this.$Ajax.post('/api/tmp/model/get', {
          modelName: v.modelPath,
        }).then(res => {
          loading.close();
          if(res.status===200){
            v.templateContent=res.data;
            this.showModel(v)
          }else {
            this.$alert(res.data.message||'读取内容失败', '错误提示', {
              confirmButtonText: '确定',
              type: 'error'
            })
          }
        })
      },
      addModel(flag) {
        const loading = this.$loading({
          background: 'rgba(0, 0, 0, 0)'
        });
        this.$Ajax.post('/api/tmp/model/save', {
          modelName: this.title=='增加模块'?this.folderPath+this.modelName:this.modelPath,
          templateContent: this.templateContent
        }).then(res => {
          loading.close();
          this.isModel=false;
          if(res.status===200){
            this.folderList=res.data
          }else {
            this.$alert(res.data.message||'失败', '错误提示', {
              confirmButtonText: '确定',
              type: 'error'
            })
          }
        })
      },
      addFolder() {
        const loading = this.$loading({
          background: 'rgba(0, 0, 0, 0)'
        });
        this.$Ajax.post('/api/tmp/save', {
          templateName: this.folderPath+this.templateName,
        }).then(res => {
          loading.close();
          this.isFolder=false;
          if(res.status===200){
            this.folderList=res.data
          }else {
            this.$alert(res.data.message||'增加失败', '错误提示', {
              confirmButtonText: '确定',
              type: 'error'
            })
          }
        })
      },
    }
  }
</script>
<style>
  .aside-tree {
    border: 1px #999999 solid;
    overflow: auto;
    height: 90vh;
    min-width: 250px !important;
    border-radius: 5px;
  }
  .aside-content{
    border: 1px #999999 solid;
    overflow: auto;
    height: 90vh;
  }
  .el-tree {
    display: inline-block !important;
  }
  .mock-col{
    padding:  10px 0;
    text-align: center;border: 1px #999999 solid;
    margin-top: 15px;
  }
  .el-icon-question{
    position: absolute;
    top: 20px;
    right: 20px;
  }
  p{
    margin:10px 0 ;
  }
  .mock_wrapper{
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 100;
    height: 100%;
    width: 100%;
  }
  .mock_help{
    transform: translateX(-50%);
    position: fixed;
    top: 60px;
    left: 50%;
    background-color: #ffffff;
    z-index: 1000;
    display: -webkit-box;
    display: -ms-flexbox;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    width:600px;display:block;
    height: 80%;
  }
  .el-icon-circle-close{
    position: absolute;
    top: 70px;
    left: 60%;
    z-index: 6666;
  }
</style>



